.mess-0 {
  width: 100%;
  max-width: 850px; /* keeps your desktop look */
  height: 700px; /* keep if you want the fixed height on desktop */
  margin: 20px;
  background-color: #ffffff;
  border-radius: 20px;
  overflow: hidden; /* keeps rounded edges clean */
}

.mess-1 {
  height: 60px;
  display: flex;
  padding: 0 0 0 20px;
  align-items: center;
  border-bottom: 1px solid #e0e0e0; /* softer border */
  font-weight: 600;
  font-size: 18px; /* slightly bigger */
}
.mess-1 h2,
.mess-head {
  color: var(--namehead);
  font-weight: 700;
}
.mess-2 {
  display: flex;
  height: calc(100% - 60px); /*  keeps full height layout */
}

.mess-3 {
  max-width: 300px;
  height: 630px; /*  fixed height instead of max-height */
  overflow-y: hidden; /*  no scroll in left bar */
  background: #fafafa;
  border-right: 1px solid #eee;
}

.mess-3::-webkit-scrollbar {
  width: 6px;
}
.mess-3::-webkit-scrollbar-thumb {
  background: #ccc;
  border-radius: 3px;
}

.mess-5 {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: antiquewhite;
}

.mess-6 {
  display: flex;
  gap: 10px;
  flex-direction: column;
  justify-content: center;
}

.mess-7 {
  display: flex;
  flex-direction: column;
  flex: 1;
  background: #fff;
}
.mess-77 {
  display: flex;
  flex-direction: column;
  flex: 1;
  background: #fff;
}

.mess-8 {
  height: 70px; /* slightly taller */
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 4px;
  padding: 0 20px;
  border-bottom: 1px solid #eee;
  background-color: white;
  font-size: 15px;
}
.convo-name-active {
  font-weight: 700;
  color: var(--namehead);
}
.mess-9 {
  background-color: white;

  flex: 1; /*  takes full space */
  overflow-y: auto;
  scrollbar-width: none;
  padding: 10px 15px; /* better spacing */
}

.mess-10 {
  padding: 12px;
  border-top: 1px solid #eee;
  background-color: white;
}

.active {
  background-color: #e6f0ff; /*  prettier active state */
}

.input-wrapper {
  position: relative;
  display: flex;
  align-items: flex-end;
  background: #fff;
  border-radius: 25px;
  border: 1px solid #f3f3f3;
  padding: 6px 12px;
  transition: border-color 0.2s;
}

.input-wrapper:focus-within {
  border-color: #007bff; /*  highlight on focus */
}

.message-input {
  flex: 1;
  padding: 10px 40px 10px 10px; /*  balanced padding */
  border: none;
  font-size: 14px;
  resize: none;
  max-height: 100px;
  overflow-y: auto;
  outline: none;
  background: transparent; /* blend with wrapper */
}

.send-btn {
  position: absolute;
  right: 10px;
  bottom: 50%;
  transform: translateY(50%);
  background-color: var(--namehead);
  color: #fff;
  border: none;
  border-radius: 50%;
  width: 34px;
  height: 34px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background 0.2s, transform 0.1s;
}

.send-btn:hover {
  background-color: var(--color-green);

  transform: translateY(50%) scale(1.05); /*  slight pop effect */
}

.char-count {
  font-size: 12px;
  color: #888;
  text-align: right;
  margin-top: 6px;
  font-style: italic; /*  softer look */
}

.mess-3-1 {
  display: flex;
  flex-direction: column;
  gap: 5px;
  padding: 10px; /*  padding inside sidebar */
}

/* Left panel container */
.left-panel {
  box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: auto;
}

/* Hide scrollbar but keep scroll functionality */
.left-panel::-webkit-scrollbar {
  width: 0;
  background: transparent;
}
.left-panel {
  scrollbar-width: none; /* Firefox */
}

.conversation-item {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 5px 16px 5px 8px;
  height: 90px; /* fixed height */
  border-bottom: 1px solid #ddd; /* fixed your missing solid */
  cursor: pointer;
  background-color: #fff;

  /* subtle shadow */
  box-shadow: var(--boxshadowmain);
  border-radius: 25px; /* optional: smooth corners */
  transition: transform 0.2s, box-shadow 0.2s;
}

.conversation-item:hover {
  transform: translateY(-2px); /* lift effect */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.12);
}

.conversation-text {
  display: -webkit-box;
  -webkit-line-clamp: 3; /* show max 3 lines */
  -webkit-box-orient: vertical; /* vertical orientation */
  overflow: hidden; /* hide extra text */
  font-size: 14px;
  color: #555;
  line-height: 1.4em;
  text-overflow: ellipsis; /* adds the  at the end */
  word-break: break-word; /* prevents overflow words */
  max-height: calc(1.4em * 3);
}

.empty-state {
  flex: 1;
  display: flex;
  justify-content: center; /* horizontal center */
  align-items: center; /* vertical center */
  flex-direction: column;
  height: 100%;
  text-align: center;
  color: #666;
  font-size: 16px;
  padding: 20px;
}

.empty-state .subtitle {
  font-size: 13px;
  margin-top: 4px;
  color: #999;
}

/* base layout (keeps your existing rules) */
.mess-2 {
  display: flex;
  position: relative;
  overflow: hidden;
}

/* left panel */
.left-panel {
  width: 380px;
  min-width: 240px;
  max-width: 45%;
  background: #fff;
  z-index: 1;
  overflow-y: auto;
  transition: opacity 0.25s ease, transform 0.25s ease;
}

/* right panel default (desktop) */
.mess-7 {
  flex: 1;
  background: #fafafa;
  z-index: 0;
  transition: transform 0.35s ease;
  display: flex;
  flex-direction: column;
}
/* small niceties */
.conversation-item {
  cursor: pointer;
}
.conversation-item.active {
  background: rgba(0, 0, 0, 0.03);
}
/* ---------- mobile behavior ---------- */

/* Mobile layout */
@media (max-width: 700px) {
  .mess-0 {
    /* let it breathe full width/height on phones */
    max-width: none;
    width: 100%;
    height: 100vh; /* fill screen */
    margin: 0; /* avoid side cut from margins */
    border-radius: 0; /* optional: edge-to-edge */
  }

  .mess-2 {
    position: relative; /* key */
    overflow: hidden; /* keeps off-screen part hidden */
  }

  /* left list uses full parent width */
  .left-panel {
    width: 100%;

    min-width: 0; /* prevent overflow on small screens */
    max-width: 100%;
  }

  /* conversation pane matches PARENT, not viewport */
  .mess-7 {
    position: absolute; /* key: inside .mess-2 */
    inset: 0; /* top/right/bottom/left = 0  fill parent */
    width: 100%;
    height: 100%;
    transform: translateX(100%); /* start off to the right */
    background: #fff;
    z-index: 2;
    transition: transform 0.35s cubic-bezier(0.22, 0.9, 0.3, 1);
    display: flex;
    flex-direction: column;
    will-change: transform;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
  }

  /* slide in when opened */
  .mess-2.mobile-open .mess-7 {
    transform: translateX(0);
  }

  /* optional dim overlay INSIDE the parent too */
  .mess-2.mobile-open::after {
    content: "";
    position: absolute;
    inset: 0;
    background: rgba(0, 0, 0, 0.25);
    z-index: 1;
  }
  /* make the header/input areas fit */
  .mess-8 {
    padding: 0 16px;
  }
  .mess-9 {
    flex: 1;
    overflow-y: auto;
  }
  .mess-10 {
    padding: 8px 12px;
  }
}
/* Desktop tweaks */
@media (min-width: 701px) {
  .mess-7 {
    position: relative;
    transform: none;
    box-shadow: none;
  }

  /* hide mobile-only back button on desktop */
  .mobile-back-btn {
    display: none;
  }

  /* hide overlay on desktop */
  .mess-2 .mobile-overlay {
    display: none !important;
  }
}

@media (min-width: 0px) and (max-width: 465px) {
  /* left becomes full width (stacked) */
  .left-panel {
    min-width: 300px;
    position: relative;
    z-index: 1;
    transition: opacity 0.25s ease, transform 0.25s ease;
  }
}
.mobile-back-btn {
  background-color: var(--namehead);
  border-radius: 30px;
  padding: 10px;
  color: white;
}
